@import url("https://fonts.googleapis.com/css2?family=Signika+Negative&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mukta:wght@200&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@200&display=swap");
body {
  margin: 0px;
  padding: 0px;
  background-color: white;
}
.profile {
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  font-family: "Nunito", sans-serif;
  padding-bottom: 40px;
  margin-top: 100px;
}
.top {
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
}
.name {
  width: auto;
  height: 100px;
  font-size: 26px;
  color: #686868;
}
.photo {
  margin-left: 300px;
  width: 160px;
  height: 160px;
  background-size: contain;
  position: relative;
}
.profile-photo {
  position: absolute;
  background-size: cover;
  width: 80%;
  height: 80%;
  left: 50%;
  top: 42%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-repeat: no-repeat;
}

.res {
  margin-top: 20px !important;
  margin-left: 0px !important;
}
.edit {
  padding: 9px;
  background-color: #007fff;
  color: white;
  outline: none;
  border: none;
  border-radius: 2px;
}
.skill {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.skill-item {
  background-color: orangered;
  color: white;
  padding: 10px;
  margin-right: 15px;
  border-radius: 4px;
  width: auto;
  display: inline-block;
}
.heading {
  color: #00308f;
  font-size: 25px !important;
  margin-left: 0px !important;
}
.bio {
  font-size: 20px;
  color: black;
}
.fnf {
  background-color: #3772ff;
  color: white;
  padding: 10px;
  border: none;
  outline: none;
}

@media only screen and (max-width: 1084px) {
  .photo {
    margin-left: 100px;
  }
}
@media only screen and (max-width: 800px) {
  .top {
    justify-content: center;
  }
  .photo {
    margin-left: -20px;
    padding-bottom: 20px;
  }
  .profile-photo {
    position: relative;
    margin-right: 20px;
    margin-top: 30px;
    height: 100px;
    width: 100px;
  }
}
